import React, { useState, useEffect } from "react";
import { useNavigate, useLocation } from "react-router-dom";
import "boxicons/css/boxicons.min.css";
import styles from "./rutas.module.css";
import Producto from "./producto.jsx";
import rutasCirculos from "./datarutas.js";
import MiniFooter from "./MiniFooter";
import Navegacion from "./navegacion";

function Rutas() {
  const [activeButton, setActiveButton] = useState(null);
  const [tapped, setTapped] = useState({});
  const [selectedProduct, setSelectedProduct] = useState(null); // Estado para el producto seleccionado
  const [screenMode, setScreenMode] = useState("standalone"); // Estado para la orientaci√≥n
  const navigate = useNavigate();
  const location = useLocation();

  useEffect(() => {
    const updateScreenMode = () => {
      if (window.matchMedia("(display-mode: standalone)").matches) {
        setScreenMode("standalone"); // Cuando est√© en modo standalone
      } else if (window.matchMedia("(orientation: portrait)").matches) {
        setScreenMode("portrait");
      } else if (window.matchMedia("(orientation: landscape)").matches) {
        setScreenMode("landscape");
      }
    };
  
    window.addEventListener("resize", updateScreenMode);
    updateScreenMode(); // Ejecutar al inicio
  
    return () => window.removeEventListener("resize", updateScreenMode);
  }, []);
  
  // Agregar un console log para verificar el valor de screenMode
  console.log(screenMode);

  const handleToggle = (id) => {
    setActiveButton((prev) => (prev === id ? null : id));
  };

  const handleCircleClick = (id) => {
    setTapped((prev) => ({
      ...Object.fromEntries(Object.keys(prev).map((key) => [key, false])),
      [id]: !prev[id],
    }));
  };

  // Funci√≥n para abrir el modal con el producto seleccionado
  const openModal = (producto) => {
    setSelectedProduct(producto);
  };

  // Funci√≥n para cerrar el modal
  const closeModal = () => {
    setSelectedProduct(null);
  };

  // Ahora usamos `require()` en lugar de importar im√°genes manualmente arriba
  const rutasPrincipales = [
    { ruta: "/salon", label: "Light", image: "../../image/tour/room.jpg" },
    { ruta: "/salon", label: "Cyborg", image: "../../image/tour/room.jpg" },
    { ruta: "/news", label: "Studio", image: "../../image/tour/room.jpg" },
  ];

  const ambienteActual = rutasPrincipales.find((item) => item.ruta === location.pathname);
  const imagenActual = ambienteActual ? ambienteActual.image : "../../image/tour/room.jpg";

  return (
    <div className={styles.Rutas}>
      <div className={styles.HoverBg}></div>
      <div className={styles.Bg}></div>
      <div className={styles.Content}>

        <Navegacion />
        <div className={styles.ImageContainer}>
          <img src={imagenActual} alt="Ambiente actual" className={styles.ResponsiveImage} />

          {rutasPrincipales.map((item, index) => (
            <div
              key={index}
              className={`${styles.IconButton} ${styles[`Button${index + 1}`]}`}
              onMouseEnter={() => {
                if (window.innerWidth > 1024) {
                  setActiveButton(index);
                }
              }}
              onMouseLeave={() => {
                if (window.innerWidth > 1024) {
                  setActiveButton(null);
                }
              }}
            >
              <i className="bx bx-right-arrow-alt" onClick={() => handleToggle(index)}></i>
              {activeButton === index && (
                <span className={styles.ButtonText} onClick={() => navigate(item.ruta)}>
                  {item.label}
                </span>
              )}
            </div>
          ))}

          {rutasCirculos.map((item, id) => (
            <div
              key={id}
              className={`${styles.PulsingCircle} ${styles[`PulsingCircle${id + 1}`]}`}
              onMouseEnter={() => {
                if (window.innerWidth > 1024) { // Solo en pantallas grandes
                  setTapped((prev) => ({ ...prev, [id]: true }));
                }
              }}
              onMouseLeave={() => {
                if (window.innerWidth > 1024) {
                  setTapped((prev) => ({ ...prev, [id]: false }));
                }
              }}
              style={{
                left: `${item.position[screenMode].x}%`, 
                top: `${item.position[screenMode].y}%`
              }}
            >
              <div onClick={() => handleCircleClick(id)} className={styles.CircleCore}></div>
              {tapped[id] && (
                <span className={`${styles.CircleText} ${styles.ShowText}`} onClick={() => openModal(item)}>
                  {item.label}
                  <span className={styles.Triangle}></span>
                </span>
              )}
            </div>
          ))}
        </div>
      </div>

      {/* Modal de pantalla completa */}
      {selectedProduct && (
        <div className={styles.modalOverlay} onClick={closeModal}>
          <div className={styles.modalContent} onClick={(e) => e.stopPropagation()}>
            <button className={styles.closeButton} onClick={closeModal}>
              <i className="bx bx-left-arrow-alt" onClick={() => handleToggle(index)}></i>
            </button>

            <Producto {...selectedProduct} currentProductId={selectedProduct.id} />
          </div>
        </div>
      )}
    </div>
  );
}

export default Rutas;