import React from "react";
import { useNavigate } from "react-router-dom";
import styles from "./navegacion.module.css"; // Importa el archivo de estilos

function Navegacion() {
  const navigate = useNavigate();

  const rutasPrincipales = [
    /* { ruta: "/", label: "Home" }, */
    { ruta: "/", label: "Mixtape" },
    { ruta: "/news", label: "News" },
  ];

  return (
    <div className={styles.Navegacion}>
      {/* Parte izquierda con 3 botones de texto */}
      <div className={styles.NavLeft}>
        {rutasPrincipales.map((item, index) => (
          <div
            key={index}
            className={styles.NavButton}
            onClick={() => navigate(item.ruta)} // Redirige a la ruta correspondiente
          >
            {item.label}
          </div>
        ))}
      </div>

      {/* Parte derecha con un bot√≥n con imagen */}
      <div className={styles.NavRight}>
        <button className={styles.ImageButton} onClick={() => navigate("/4b")}>
          <img src="https://img.freepik.com/premium-vector/1x1-logo_764382-138951.jpg?w=360" alt="Profile" className={styles.ImageIcon} />
        </button>
      </div>
    </div>
  );
}

export default Navegacion;