import { useState } from "react";
import { Swiper, SwiperSlide } from "swiper/react";
import { Autoplay, EffectFade, Pagination } from "swiper/modules";  // Importar Pagination
import "swiper/css";
import "swiper/css/effect-fade";
import "swiper/css/pagination";
import styles from "./producto.module.css";
import MiniFooter from "./MiniFooter";

const Producto = ({ name, description = "", features, images, creators, urls, currentProductId }) => {
  const [activeIndex, setActiveIndex] = useState(0);

  return (
    <div className={styles.container}>
      {/* Secci√≥n del Swiper */}
      <div className={styles.imageContainer}>
        <Swiper
          spaceBetween={0}
          slidesPerView={1}
          onSlideChange={(swiper) => setActiveIndex(swiper.activeIndex)}
          className={styles.swiper}
          loop={true}
          speed={1500}
          autoplay={{ delay: 3000, disableOnInteraction: false }}
          modules={[Autoplay, EffectFade, Pagination]}
          effect="fade"
          pagination={{
            clickable: true,
            type: "none", // bullets
          }}
          touchRatio={0.2}
        >
          {images.map((src, index) => (
            <SwiperSlide key={index}>
              <img src={src} alt="Producto" className={styles.productImage} />
            </SwiperSlide>
          ))}
        </Swiper>
      </div>

      {/* Secci√≥n de informaci√≥n */}
      <div className={styles.infoContainer}>
        <h1 className={styles.productName}>{name}</h1>

        {description && (
          <p className={styles.productDescription}>
            {description.split("\n").map((line, index) => (
              <span key={index}>{line}<br /></span>
            ))}
          </p>
        )}

        <ul className={styles.featuresList}>
          {features.map((feature, index) => (
            <li key={index} className={styles.featureItem}>‚ú¶ {feature}</li>
          ))}
        </ul>

        {/* üîπ Secci√≥n de Creadores */}
        <div className={styles.creators}>
          {creators.map((creator, index) => (
            <div key={index} className={styles.creator}>
              <img src={creator.image} alt={creator.name} className={styles.creatorImage} />
              <a 
                href={creator.link} 
                target="_blank" 
                rel="noopener noreferrer" 
                className={styles.creatorButton}
              >
                {creator.name}
              </a>
            </div>
          ))}
        </div>

        {/* Botones de URL */}
        <div className={styles.buttons}>
          {urls.map((btn, index) => (
            <a 
              key={index} 
              href={btn.link} 
              className={styles.button}
              target="_blank" 
              rel="noopener noreferrer"
            >
              {btn.name}
            </a>
          ))}
        </div>

        {/* Pasamos currentProductId al MiniFooter */}
        <MiniFooter currentProductId={currentProductId} />
      </div>
    </div>
  );
};

export default Producto;